
html {
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;

  @media #{$media-480} {
    font-size: 10px;
  }

  @media #{$media-780} {
    font-size: 13px;
  }
}

body {
  font-family: $font-stack;
  font-weight: normal;
  margin: 0;

  > * {
      width: 100%;
      display: inline-block;
  }
}

img {
  max-width: 100%;
  max-height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.background-white {
  background: $white;
}

.background-rose {
  background: $setcon-coral;
}

.background-dark-red {
  background: $setcon-dark-coral;
}

.stripe-gray {
  height: 20px;
  background-color: $setcon-dark-gray;
}

.stripe-dark-coral {
  height: 20px;
  background-color: $setcon-dark-coral;
}

.stripe-white {
  height: 20px;
}

@mixin flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@mixin flex-row-space-around {
  @include flex-row;
  justify-content: space-around;
}

@mixin flex-row-space-between {
  @include flex-row;
  justify-content: space-between;
}